        ></a>
      </div>
    </div>

    <number-keyboard
      :isOpen="setTradePwbIsOpen"
      :message="setTradePwbMsg"
      @close="setTradePwbClose"
      @submit="setTradePwbSubmit"
    ></number-keyboard>
  </div>
