<template>
  <div class="page bg-white">
    <div class="page-top page-new-list-top row bg-primary">
      <div class="col-2 pl-0">
        <a href="javascript:history.go(-1)" class="pl-3 d-block">
          <span class="fas fa-chevron-left text-white"></span>
        </a>
      </div>
      <div class="text-center col-8"></div>
    </div>
    <div class="row">
      <div class="col-12 pt-2 pb-2 text-center text-muted">
        
        <strong class="text-primary">{{ inviteCount }}</strong> 
      </div>
      <div class="col-12 bg-light pt-2"></div>
    </div>

    <div class="row pt-3" v-for="item in inviteList">
      <div class="col-2" v-if="item.user_info && item.user_info.avatar ">
        <img :src="item.user_info.avatar" alt width="36" height="36" class="rounded-circle">
      </div>
      <div class="col-2" v-else>
        <img src="./../../images/avatar.png" alt width="36" height="36" class="rounded-circle">
      </div>
      <div class="col-5">{{ item.user_info ? item.user_info.realname : ''}}</div>
      <div class="col-5 text-muted text-right">{{ formatTime(item.create_time) }}</div>
      <div class="col-12">
        <hr>
      </div>
    </div>
  </div>
</template>

<script>
import Moment from "moment";
export default {
  asyncData({ store, route }) {
    store.dispatch("inviteListGet", { route: route });
  },
  computed: {
    inviteList() {
      return this.$store.state.inviteList;
    },
    inviteCount() {
      return this.$store.state.inviteCount;
    }
  },
  methods: {
    formatTime(timestamp, format = "YYYY-MM-DD HH:mm") {
      let date = new Date(timestamp * 1000);
      return Moment(date).format(format);
    }
  }
};
</script>
